import { useState } from "react";

interface Props {
  label: any;
  name: any;
  handleChange: any;
  arr: any;
  text: any;
  error: any;
  hasSubmited:any;
  value:any;
}

const SelectInput: React.FC<Props> = (props: Props) => {
  const validateInput = (checkingText: string) => {
    props.handleChange(checkingText, props.name);
  };

  return (
    <div className="mb-3">
      <label className={"form-label " + (!props.error[props.name] && props.hasSubmited  ? "base-error" : "")}>
        {props.label}
      </label>
      <select
        onChange={(event) => {
          validateInput(event.target.value);
        }}
        defaultValue={props.value}
        style={{ border: !props.error[props.name] && props.hasSubmited ? "1px solid red" : "" }}
        className="form-select"
      >
        <option selected>Open this select menu</option>
        {props.arr.map((value: any) => {
          return <option value={value[props.name]}>{value[props.text]}</option>;
        })}
      </select>
      <p className="base-error">{!props.error[props.name] && props.hasSubmited ? "This field is required" : ""}</p>
    </div>
  );
};

export default SelectInput;
