// components
import GroupedBar from "../../../../components/Charts/BarChart/GroupedBar";
import HttpClient from "../../../../services/HttpClient/HttpClient";
import Pie from "../../../../components/Charts/PieChart/Pie";
import Text from "../../../../components/Text/Text";
import BasicLine from "../../../../components/Charts/LineChart/Basic";

// icon
import DownloadIcon from "@mui/icons-material/Download";
import FilterListIcon from "@mui/icons-material/FilterList";
// module
import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router";
// css
import "../../../../stylesheet/Statistic.css";

const ExpenseStatistic: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const { projectID, memberID } = useParams<any>();
  const [expenseByDay, setExpenseByDay] = useState<any>({});
  const [cost, setCost] = useState<any>({});
  const [expenseByMonth, setExpenseByMonth] = useState<any>({});
  const selectors: any = useSelector((allSelectors) => allSelectors);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const loadSummary = async (): Promise<void> => {
    if (selectors.memberInfo.permission) {
      var result = await HttpClient.post("ExpenseStatistic/LoadSummary", {
        manager_id: memberID,
        project_id: projectID,
      });
      console.log(result);
      
      setExpenseByMonth(result.expenseByMonth);
      setExpenseByDay(result.expenseByDay);
      setCost(result.cost);
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getTotal = (value: any, name: string, groupName: string): any => {
    if (value[name] !== undefined) {
      var total = 0;
      value[name].map((value: any) => {
        total += value[groupName];
      });
      return total;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getExpense = (value: any, name: string): any => {
    if (value[name] !== undefined) {
      var arr: any = [];
      value[name].map((value: any) => {
        arr.push(value.count_expense);
      });
      return arr;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getDay = (name: any): any => {
    if (expenseByDay[name] !== undefined) {
      var arr: any = [];
      expenseByDay[name].map((value: any) => {
        arr.push(value.expense_date.substring(0, 10));
      });
      return arr;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const totalExpenseByMonth = (): number => {
    if (expenseByMonth.approval !== undefined) {
      var sum: number = 0;
      expenseByMonth.approval.map((value: any) => {
        sum += value.count_expense;
      });
      expenseByMonth.unapproval.map((value: any) => {
        sum += value.count_expense;
      });
      return sum;
    }
    return 0;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const sumExpenseByMonth = (name: string): number => {
    if (expenseByMonth[name] !== undefined) {
      var sum: number = 0;
      expenseByMonth[name].map((value: any) => {
        sum += value.count_expense;
      });
      return sum;
    }
    return 0;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  useEffect(() => {
    if (!expenseByDay.length && !expenseByMonth.length) loadSummary();
  }, []);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="statistic">
      <div className="header">
        <h1 className="m-0">Welcome back Timesheet !</h1>
      </div>
      <div className="row w-100 m-0">
        <div className="col-xl-6 col-12 mb-2"></div>
        <div className="col-xl-6 col-12 mb-2  pe-3 pt-3">
          <div className="table-tool">
            <Text style={{ marginLeft: "10px", cursor: "pointer" }}>
              <p className="d-flex align-items-center">
                <FilterListIcon />
                <span style={{ marginLeft: "10px" }}>Filter</span>
              </p>
            </Text>
            <Text style={{ marginLeft: "10px", cursor: "pointer" }}>
              <p className="d-flex align-items-center">
                <DownloadIcon />
                <span style={{ marginLeft: "10px" }}>Export</span>
              </p>
            </Text>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="summary d-flex">
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Approval</h5>
              <h1 style={{ color: "#2196f3" }}>{cost.approval}</h1>
              <h4 style={{ color: "#2196f3" }}>Cost</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Unapproval</h5>
              <h1 style={{ color: "#f44336" }}>{cost.unapproval}</h1>
              <h4 style={{ color: "#f44336" }}>Cost</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Approval</h5>
              <h1 style={{ color: "#f59e0b" }}>
                {getTotal(expenseByDay, "approval", "count_expense")}
              </h1>
              <h4 style={{ color: "#f59e0b" }}>Sheets</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Unapproval</h5>
              <h1 style={{ color: "#22c55e" }}>
                {getTotal(expenseByDay, "approval", "count_expense")}
              </h1>
              <h4 style={{ color: "#22c55e" }}>Sheets</h4>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Expense Summary By Month
          </p>
          <div className="row">
            <div className="col-xl-9">
              <GroupedBar
                ds1Name="Approved"
                ds2Name="Unapproved"
                ds1={getExpense(expenseByMonth, "approval")}
                ds2={getExpense(expenseByMonth, "unapproval")}
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <div className="number-statistic-box">
                  <div style={{ background: "#eef2ff" }} className="info">
                    <span>
                      <h1 style={{ color: "#3730a3" }} className="text-center">
                        {totalExpenseByMonth()}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#3730a3" }}
                      >
                        Expense
                      </p>
                    </span>
                  </div>
                </div>
                <div className="number-statistic-box">
                  <div style={{ background: "#f0fdf4" }} className="info">
                    <span>
                      <h1 style={{ color: "#166534" }} className="text-center">
                        {sumExpenseByMonth("approval")}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#166534" }}
                      >
                        Approval Expense
                      </p>
                    </span>
                  </div>
                </div>
                <div className="number-statistic-box">
                  <div style={{ background: "#f1f5f9" }} className="info">
                    <span>
                      <h1 style={{ color: "#64748b " }} className="text-center">
                        {sumExpenseByMonth("unapproval")}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#64748b " }}
                      >
                        Unapproval Expense
                      </p>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Expense Summary By Day
          </p>
          <div className="row">
            <div className="col-xl-9">
              <BasicLine
                dsName="Approval"
                ds={getExpense(expenseByDay, "approval")}
                labels={getDay("approval")}
                color="#252f3e"
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <Pie
                  ds1Name="Approval"
                  ds2Name="Unapproved"
                  ds1={sumExpenseByMonth("approval")}
                  ds2={sumExpenseByMonth("unapproval")}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Expense Summary By Day
          </p>
          <div className="row">
            <div className="col-xl-9">
              <BasicLine
                dsName="Unapproved"
                ds={getExpense(expenseByDay, "unapproval")}
                labels={getDay("unapproval")}
                color="#22d3ee"
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <Pie
                  ds1Name="Approval"
                  ds2Name="Unapproved"
                  ds1={sumExpenseByMonth("approval")}
                  ds2={sumExpenseByMonth("unapproval")}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExpenseStatistic;
