import * as React from "react";
import { styled, alpha } from "@mui/material/styles";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import IconButton from "@mui/material/IconButton";
import Typography from "@mui/material/Typography";
import InputBase from "@mui/material/InputBase";
import Badge from "@mui/material/Badge";
import MenuItem from "@mui/material/MenuItem";
import Menu from "@mui/material/Menu";
import SearchIcon from "@mui/icons-material/Search";
import MailIcon from "@mui/icons-material/Mail";
import NotificationsIcon from "@mui/icons-material/Notifications";
import MoreIcon from "@mui/icons-material/MoreVert";
import { NavLink } from "react-router-dom";
import HomeIcon from "@mui/icons-material/Home";
import InfoIcon from "@mui/icons-material/Info";
import FileDownloadIcon from "@mui/icons-material/FileDownload";
import Avatar from "../../Avatar/Avatar";
import DashboardIcon from "@mui/icons-material/Dashboard";
import ManageAccountsIcon from "@mui/icons-material/ManageAccounts";
import ExitToAppIcon from "@mui/icons-material/ExitToApp";
import { useSelector, useDispatch } from "react-redux";
import { useState } from "react";
import { Button } from "@mui/material";
import NativeStorage from "../../../services/NativeStorage/NativeStorage";
import { useHistory } from "react-router";
import QrCodeScannerIcon from "@mui/icons-material/QrCodeScanner";
import ListAltIcon from "@mui/icons-material/ListAlt";
// global functions
const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(3),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    width: "100%",
    [theme.breakpoints.up("md")]: {
      width: "20ch",
    },
  },
}));

const Navbar: React.FC = () => {
  const dispatch = useDispatch();
  const socket = useSelector((allSelectors: any) => allSelectors.socket);
  const memberInfo = useSelector(
    (allSelectors: any) => allSelectors.memberInfo
  );
  const isLogedIn = useSelector((allSelectors: any) => allSelectors.isLogedIn);
  const history = useHistory();
  const [isLogedOut,setIsLogedOut] = useState<boolean>(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event: any) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event: any) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  const logOut = async (): Promise<void> => {
    history.push("/home");
    socket.emit("Offline", memberInfo.member_id);
    await NativeStorage.clear();
    dispatch({
      type: "SET_STATE",
      payload: {
        memberInfo: {
          member_id: "",
          first_name: "",
          last_name: "",
          email: "",
          permission: "",
          avatar: "",
        },
        isLogedIn: false,
        project_id: "",
      },
    });
  };

  const menuId = "primary-search-account-menu";
  const renderMenu = (
    <Menu
      PaperProps={{
        style: {
          width: 350,
        },
      }}
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>
        <div className="row p-0 m-0 w-100">
          <div className="col-3">
            <IconButton
              size="large"
              aria-label="show 17 new notifications"
              color="inherit"
              onClick={handleProfileMenuOpen}
            >
              <Avatar
                name={memberInfo.first_name + " " + memberInfo.last_name}
                src={memberInfo.avatar}
              />
            </IconButton>
          </div>
          <div className="col-9">
            <div className="h-100 d-flex">
              <span className="mt-auto mb-auto">
                <div>{memberInfo.email}</div>
                <div>{memberInfo.first_name + " " + memberInfo.last_name}</div>
              </span>
            </div>
          </div>
        </div>
      </MenuItem>
      <div className="dropdown-divider" />
      {memberInfo.permission === "ProductOwner" ? (
        <MenuItem onClick={handleMenuClose}>
          <NavLink className="base-link" to="/dashboard">
            <DashboardIcon />
            <span className="ms-4">Dashboard</span>
          </NavLink>
        </MenuItem>
      ) : (
        <MenuItem onClick={handleMenuClose}>
          <NavLink className="base-link" to="/dashboard">
            <ListAltIcon />
            <span className="ms-4">Project List</span>
          </NavLink>
        </MenuItem>
      )}
      <MenuItem onClick={handleMenuClose}>
        <NavLink className="base-link" to="/dashboard">
          <ManageAccountsIcon />
          <span className="ms-4">Profile</span>
        </NavLink>
      </MenuItem>
      <div className="dropdown-divider" />
      <MenuItem
        onClick={() => {
          handleMenuClose();
          logOut();
        }}
      >
        <NavLink className="base-link" to="/dashboard">
          <ExitToAppIcon />
          <span className="ms-4">Logout</span>
        </NavLink>
      </MenuItem>
    </Menu>
  );

  const mobileMenuId = "primary-search-account-menu-mobile";
  const renderMobileMenu = (
    <Menu
      PaperProps={{
        style: {
          width: 300,
        },
      }}
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{
        vertical: "top",
        horizontal: "right",
      }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
      <NavLink
        style={{
          textDecoration: "none",
          color: "black",
        }}
        to="/home"
      >
        <MenuItem>
          <IconButton
            size="large"
            aria-label="show 4 new mails"
            color="inherit"
          >
            <HomeIcon />
          </IconButton>
          <p className="m-0">Home</p>
        </MenuItem>
      </NavLink>

      <NavLink
        style={{
          textDecoration: "none",
          color: "black",
        }}
        to="/home"
      >
        <MenuItem>
          <IconButton
            size="large"
            aria-label="show 4 new mails"
            color="inherit"
          >
            <InfoIcon />
          </IconButton>
          <p className="m-0">About</p>
        </MenuItem>
      </NavLink>
      <NavLink
        style={{
          textDecoration: "none",
          color: "black",
        }}
        to="/home"
      >
        <MenuItem>
          <IconButton
            size="large"
            aria-label="show 4 new mails"
            color="inherit"
          >
            <FileDownloadIcon />
          </IconButton>
          <p className="m-0">Download</p>
        </MenuItem>
      </NavLink>
      <NavLink
        style={{
          textDecoration: "none",
          color: "black",
        }}
        to="/qr-scanner"
      >
        <MenuItem>
          <IconButton
            size="large"
            aria-label="show 4 new mails"
            color="inherit"
          >
            <QrCodeScannerIcon />
          </IconButton>
          <p className="m-0">QR Scanner</p>
        </MenuItem>
      </NavLink>
      <MenuItem>
        <IconButton size="large" aria-label="show 4 new mails" color="inherit">
          <MailIcon />
        </IconButton>
        <p className="m-0">Messages</p>
      </MenuItem>
      <MenuItem>
        <IconButton
          size="large"
          aria-label="show 17 new notifications"
          color="inherit"
        >
          <Badge badgeContent={17} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <p className="m-0">Notifications</p>
      </MenuItem>
    </Menu>
  );

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <Typography
            variant="h6"
            noWrap
            component="div"
            sx={{ display: { xs: "none", sm: "block" } }}
          >
            Timesheet
          </Typography>
          <Search>
            <SearchIconWrapper>
              <SearchIcon />
            </SearchIconWrapper>
            <StyledInputBase
              placeholder="Search…"
              inputProps={{ "aria-label": "search" }}
            />
          </Search>
          <Box sx={{ flexGrow: 1 }}></Box>
          <Box sx={{ display: { xs: "none", md: "flex" } }}>
            <NavLink
              style={{
                textDecoration: "none",
                color: "white",
                margin: "0",
                padding: "0",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
              to="/home"
            >
              <IconButton
                size="large"
                aria-label="show 4 new mails"
                color="inherit"
              >
                <HomeIcon />
              </IconButton>
            </NavLink>

            <NavLink
              style={{
                textDecoration: "none",
                color: "white",
                margin: "0",
                padding: "0",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
              to="/home"
            >
              <IconButton
                size="large"
                aria-label="show 4 new mails"
                color="inherit"
              >
                <InfoIcon />
              </IconButton>
            </NavLink>
            <NavLink
              style={{
                textDecoration: "none",
                color: "white",
                margin: "0",
                padding: "0",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
              }}
              to="/home"
            >
              <IconButton
                size="large"
                aria-label="show 4 new mails"
                color="inherit"
              >
                <FileDownloadIcon />
              </IconButton>
            </NavLink>

            <IconButton
              size="large"
              aria-label="show 4 new mails"
              color="inherit"
            >
              <Badge badgeContent={4} color="error">
                <MailIcon />
              </Badge>
            </IconButton>
            <IconButton
              size="large"
              aria-label="show 17 new notifications"
              color="inherit"
            >
              <Badge badgeContent={17} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>
            {isLogedIn ? (
              <IconButton
                size="large"
                edge="end"
                aria-label="show 17 new notifications"
                aria-controls={menuId}
                color="inherit"
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
              >
                <Avatar
                  name={memberInfo.first_name + " " + memberInfo.last_name}
                  src={memberInfo.avatar}
                />
              </IconButton>
            ) : (
              <NavLink
                style={{
                  textDecoration: "none",
                  color: "black",
                  margin: "0",
                  padding: "0",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  marginLeft: "10px",
                }}
                to="/login"
              >
                <Button
                  size="small"
                  sx={{ width: "90px", fontSize: "11px" }}
                  color="inherit"
                  variant="contained"
                >
                  Login Now
                </Button>
              </NavLink>
            )}
          </Box>
          <Box sx={{ display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <MoreIcon />
            </IconButton>
            {isLogedIn ? (
              <IconButton
                size="large"
                edge="end"
                aria-label="show 17 new notifications"
                aria-controls={menuId}
                color="inherit"
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
              >
                <Avatar
                  name={memberInfo.first_name + " " + memberInfo.last_name}
                  src={memberInfo.avatar}
                />
              </IconButton>
            ) : (
              <NavLink
                style={{
                  textDecoration: "none",
                  color: "black",
                  margin: "0",
                  padding: "0",
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                  marginLeft: "10px",
                }}
                to="/login"
              >
                <Button
                  size="small"
                  sx={{ width: "90px", fontSize: "11px" }}
                  color="inherit"
                  variant="contained"
                >
                  Login Now
                </Button>
              </NavLink>
            )}
          </Box>
        </Toolbar>
      </AppBar>
      {renderMobileMenu}
      {renderMenu}
    </Box>
  );
};

export default Navbar;
