import * as React from "react";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import ListItemAvatar from "@mui/material/ListItemAvatar";
import Avatar from "@mui/material/Avatar";
import { styled } from "@mui/material/styles";
import Badge from "@mui/material/Badge";
import MyAvatar from "../../../../../../components/Avatar/Avatar";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  avatar: string;
  name: string;
  message: string;
  recentDate: Date;
  isOnline: Boolean;
}
//--------------------------------------------------------------#e67e22
//--------------------------------------------------------------
const ListFriend: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const StyledBadge = styled(Badge)(({ theme }) => ({
    "& .MuiBadge-badge": {
      backgroundColor: (props.isOnline?"#44b700":"#e67e22"),
      color: (props.isOnline?"#44b700":"#e67e22"),
      boxShadow: `0 0 0 2px ${theme.palette.background.paper}`,
      "&::after": {
        position: "absolute",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        borderRadius: "50%",
        animation: "ripple 1.2s infinite ease-in-out",
        border: "1px solid currentColor",
        content: '""',
      },
    },
    "@keyframes ripple": {
      "0%": {
        transform: "scale(.8)",
        opacity: 1,
      },
      "100%": {
        transform: "scale(2.4)",
        opacity: 0,
      },
    },
  }));
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const SmallAvatar = styled(Avatar)(({ theme }) => ({
    width: 22,
    height: 22,
    border: `2px solid ${theme.palette.background.paper}`,
  }));
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <ListItem style={{ cursor: "pointer" }} alignItems="flex-start">
      <ListItemAvatar>
        <StyledBadge
          overlap="circular"
          anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
          variant="dot"
        >
          <MyAvatar src={props.avatar} name={props.name} />
        </StyledBadge>
      </ListItemAvatar>
      <ListItemText
        primary={props.name}
        secondary={<React.Fragment>{props.message}</React.Fragment>}
      />
      <p style={{ fontSize: "13px", color: "#64748b" }}>
        {props.recentDate.toLocaleDateString()}
      </p>
    </ListItem>
  );
};

export default ListFriend;
