import { useEffect, useState } from "react";
import QRCode from "react-qr-code";
import { useSelector } from "react-redux";
import "./TimesheetCode.css";
import Button from "@mui/material/Button/Button";
import HttpClient from "../../../../services/HttpClient/HttpClient";
//--------------------------------------------------------------
//--------------------------------------------------------------
const TimesheetCode: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const [status, setStatus] = useState<boolean>(false);
  const [qrCode, setQrCode] = useState<string>("");
  const [err, setErr] = useState<string>("");
  const memberInfo = useSelector(
    (allSelectors: any) => allSelectors.memberInfo
  );
  const project_id = useSelector(
    (allSelectors: any) => allSelectors.project_id
  );
  const socketID = useSelector((allSelectors: any) => allSelectors.socketID);
  const socket = useSelector((allSelectors: any) => allSelectors.socket);
  const isConnectedSocket = useSelector(
    (allSelectors: any) => allSelectors.isConnectedSocket
  );
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const determineTime = (): string => {
    var date = new Date();
    if (date.getHours() >= 7 && date.getHours() <= 12) return "Morning Shift";
    if (date.getHours() >= 13 && date.getHours() <= 17)
      return "Afternoon Shift";
    if (date.getHours() > 17 && date.getHours() <= 21) return "Night Shift";
    return "";
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const takeAttendant = async (): Promise<void> => {
    try {
      var result = await HttpClient.post("TimesheetReport/SubmitTimesheet", {
        project_id: project_id,
        employee_id: memberInfo.member_id,
      });
      if (!result) setErr("Fail to take attendant");
      setStatus(false);
    } catch (error: any) {
      console.log(error);
      setErr("Error : " + error.message);
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  useEffect(() => {
    if (isConnectedSocket) {
      var newValue = { ...memberInfo };
      newValue.socketID = socketID;
      console.log(newValue);
      setQrCode(JSON.stringify(newValue));
      socket.on("TakeAttendant", (info: any) => {
        //alert("ok");
        setStatus(true);
      });
    }
  }, [socket]);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="timesheet-code">
      <div className="m-auto p-3">
        <div className="d-flex justify-content-center">
          <QRCode
            value={qrCode}
            // bgColor="#FFFFFF"
            // fgColor="#000000"
            // level="Q"
            // size={500}
            title={"nsdf"}
          />
        </div>
        <p className="description mt-3 text-center">
          Put the QR Code in font of the Scanner to take attendant
        </p>
        {status ? (
          <div style={{ display: "grid" }} className="align-items-center">
            <h4 className="text-center">
              You are working in the {determineTime()}
            </h4>
            <Button onClick={takeAttendant} variant="contained">
              Take Attendant
            </Button>
          </div>
        ) : (
          ""
        )}
        {err ? <div className="base-error">{err}</div> : ""}
      </div>
    </div>
  );
};
//--------------------------------------------------------------
//--------------------------------------------------------------
export default TimesheetCode;
