//--------------------------------------------------------------
//--------------------------------------------------------------
// base state
const initialState = {
  project_id:"",
  isConnectedSocket: false,
  isLogedIn:false,
  memberInfo: {
    member_id: "",
    first_name: "",
    last_name: "",
    email: "",
    permission: "",
    avatar: "",
  },
};

//--------------------------------------------------------------
//--------------------------------------------------------------

type Action = { type: "SET_STATE"; payload: any };

// payload function
export const reducer = (state: any = initialState, action: Action) => {
  const { type, payload } = action;
  switch (type) {
    case "SET_STATE":
      return { ...state, ...payload };
    default:
      return state;
  }
};

