// components
import GroupedBar from "../../../../components/Charts/BarChart/GroupedBar";
import HttpClient from "../../../../services/HttpClient/HttpClient";
import Pie from "../../../../components/Charts/PieChart/Pie";
import Text from "../../../../components/Text/Text";
import BasicLine from "../../../../components/Charts/LineChart/Basic";
// icon
import DownloadIcon from "@mui/icons-material/Download";
import FilterListIcon from "@mui/icons-material/FilterList";
// module
import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
// css
import "../../../../stylesheet/Statistic.css";

const BudgetProject: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const [costByDay, setCostByDay] = useState<any>({});
  const [expenseNumber, setExpenseNumber] = useState<any>({});
  const [costByMonth, setCostByMonth] = useState<any>({});
  const [isShownFilter, setIsShownFilter] = useState(false);
  const [projects, setProjects] = useState([]);
  const selectors: any = useSelector((allSelectors) => allSelectors);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const loadSummary = async (project_id: string): Promise<void> => {
    if (project_id) {
      var result = await HttpClient.post("BudgetProject/LoadSummary", {
        project_id: project_id,
      });
      setCostByDay(result.costByDay);
      setCostByMonth(result.costByMonth);
      setExpenseNumber(result.expenseNumber);
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const loadProjects = async (): Promise<void> => {
    if (selectors.memberInfo.permission) {
      var result = await HttpClient.post("ProjectManagement/LoadProject", {
        member_id: selectors.memberInfo.member_id,
      });
      if (result) {
        setProjects(result);
        loadSummary(result[0].project_id);
      }
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getTotal = (value: any, name: string, groupName: string): any => {
    if (value[name] !== undefined) {
      var total = 0;
      value[name].map((value: any) => {
        total += value[groupName];
      });
      return total;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getCost = (value: any, name: string): any => {
    if (value[name] !== undefined) {
      var arr: any = [];
      value[name].map((value: any) => {
        arr.push(value.sum_cost);
      });
      return arr;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getDay = (name: any): any => {
    if (costByDay[name] !== undefined) {
      var arr: any = [];
      costByDay[name].map((value: any) => {
        arr.push(value.expense_date.substring(0, 10));
      });
      return arr;
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const totalCostByMonth = (): number => {
    if (costByMonth.approval !== undefined) {
      var sum: number = 0;
      costByMonth.approval.map((value: any) => {
        sum += value.sum_cost;
      });
      costByMonth.unapproval.map((value: any) => {
        sum += value.sum_cost;
      });
      return sum;
    }
    return 0;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const sumCostByMonth = (name: string): number => {
    if (costByMonth[name] !== undefined) {
      var sum: number = 0;
      costByMonth[name].map((value: any) => {
        sum += value.sum_cost;
      });
      return sum;
    }
    return 0;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const FilterForm: React.FC = () => {
    if (isShownFilter) {
      return (
        <div className="mp">
          <select
            onChange={(e: any) => {
              loadSummary(e.target.value);
            }}
            className="form-control w-50"
          >
            {projects.map((value: any) => {
              return (
                <option value={value.project_id}>{value.project_name}</option>
              );
            })}
          </select>
        </div>
      );
    }
    return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  useEffect(() => {
    if (!projects.length) loadProjects();
  }, []);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="statistic">
      <div className="header">
        <h1 className="m-0">Welcome back Timesheet !</h1>
      </div>
      <div className="row w-100 m-0">
        <div className="col-xl-6 col-12 mb-2"></div>
        <div className="col-xl-6 col-12 mb-2">
          <div className="table-tool pe-3 pt-3">
            <Text
              style={{ marginLeft: "10px", cursor: "pointer" }}
              onClick={() => {
                setIsShownFilter(!isShownFilter);
              }}
            >
              <p className="d-flex align-items-center">
                <FilterListIcon />
                <span style={{ marginLeft: "10px" }}>Filter</span>
              </p>
            </Text>
            <Text style={{ marginLeft: "10px", cursor: "pointer" }}>
              <p className="d-flex align-items-center">
                <DownloadIcon />
                <span style={{ marginLeft: "10px" }}>Export</span>
              </p>
            </Text>
          </div>
        </div>
      </div>
      <FilterForm />
      <div className="mp">
        <div className="summary d-flex">
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Approval</h5>
              <h1 style={{ color: "#2196f3" }}>
                {getTotal(costByDay, "approval", "sum_cost")}
              </h1>
              <h4 style={{ color: "#2196f3" }}>Cost</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Unapproval</h5>
              <h1 style={{ color: "#f44336" }}>
                {getTotal(costByDay, "unapproval", "sum_cost")}
              </h1>
              <h4 style={{ color: "#f44336" }}>Cost</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Approval</h5>
              <h1 style={{ color: "#f59e0b" }}>{expenseNumber.approval}</h1>
              <h4 style={{ color: "#f59e0b" }}>Sheets</h4>
            </div>
          </div>
          <div className="w-25 p-3">
            <div className="summary-box">
              <h5 style={{ color: "#6b7280" }}>Unapproval</h5>
              <h1 style={{ color: "#22c55e" }}>{expenseNumber.unapproval}</h1>
              <h4 style={{ color: "#22c55e" }}>Sheets</h4>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Cost Summary By Month
          </p>
          <div className="row">
            <div className="col-xl-9">
              <GroupedBar
                ds1Name="Approved"
                ds2Name="Unapproved"
                ds1={getCost(costByMonth, "approval")}
                ds2={getCost(costByMonth, "unapproval")}
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <div className="number-statistic-box">
                  <div style={{ background: "#eef2ff" }} className="info">
                    <span>
                      <h1 style={{ color: "#3730a3" }} className="text-center">
                        {totalCostByMonth()}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#3730a3" }}
                      >
                        Cost
                      </p>
                    </span>
                  </div>
                </div>
                <div className="number-statistic-box">
                  <div style={{ background: "#f0fdf4" }} className="info">
                    <span>
                      <h1 style={{ color: "#166534" }} className="text-center">
                        {sumCostByMonth("approval")}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#166534" }}
                      >
                        Approval Cost
                      </p>
                    </span>
                  </div>
                </div>
                <div className="number-statistic-box">
                  <div style={{ background: "#f1f5f9" }} className="info">
                    <span>
                      <h1 style={{ color: "#64748b " }} className="text-center">
                        {sumCostByMonth("unapproval")}
                      </h1>
                      <p
                        className="m-0 text-center"
                        style={{ color: "#64748b " }}
                      >
                        Unapproval Cost
                      </p>
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Cost Summary By Day
          </p>
          <div className="row">
            <div className="col-xl-9">
              <BasicLine
                dsName="Approval"
                ds={getCost(costByDay, "approval")}
                labels={getDay("approval")}
                color="#252f3e"
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <Pie
                  ds1Name="Approval"
                  ds2Name="Unapproved"
                  ds1={sumCostByMonth("approval")}
                  ds2={sumCostByMonth("unapproval")}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="mp">
        <div className="statistic-box mt-3">
          <p style={{ fontSize: "20px" }} className="mt-3 mb-3">
            Cost Summary By Month
          </p>
          <div className="row">
            <div className="col-xl-9">
              <BasicLine
                dsName="Unapproved"
                ds={getCost(costByDay, "unapproval")}
                labels={getDay("unapproval")}
                color="#22d3ee"
              />
            </div>
            <div className="col-xl-3">
              <div className="w-100 h-100">
                <Pie
                  ds1Name="Approval"
                  ds2Name="Unapproved"
                  ds1={sumCostByMonth("approval")}
                  ds2={sumCostByMonth("unapproval")}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default BudgetProject;
