// components
import Capcha from "../../components/Capcha/Capcha";
import SubmitButton from "../../components/Button/SubmitButton";
import TextAlert from "../../components/Alert/Text/TextAlert";
import { IonContent, IonPage } from "@ionic/react";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import BaseInput from "../../components/FormValidation/BaseInput";
import NumberInput from "../../components/FormValidation/NumberInput";
import PasswordInput from "../../components/FormValidation/PasswordInput";
// module
import { Link } from "react-router-dom";
import React, { useEffect, useState } from "react";
import HttpClient from "../../services/HttpClient/HttpClient";
import $ from "jquery";
// stylesheet
import "./Register.css";
//--------------------------------------------------------------
//--------------------------------------------------------------
const Register: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  // declare variables
  const [isLoaded, setIsLoaded] = useState<boolean>(true);
  const [error, setError] = useState<string>("");
  const [capchaCode, setCapchaCode] = useState<string>("");
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const [user, setUser] = useState<any>({
    email: "",
    password: "",
    first_name: "",
    last_name: "",
    street_code: "",
    street_name: "",
    area: "",
    city: "",
    country: "",
    confirm_password: "",
  });
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const handleChange = (event: any, name: string): void => {
    var newUser: any = { ...user };
    newUser[name] = event.target.value;
    setError("");
    setUser(newUser);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const authenticate = async (e: any): Promise<void> => {
    e.preventDefault();
    var isValid = true;
    for (var item in user) if (!user[item]) isValid = false;
    if (isValid) {
      if (user.confirm_password === user.password) {
        var capchaTextTemp = $("#register_capcha").attr("data-text");
        if (capchaTextTemp === capchaCode) {
          setIsLoaded(false);
          var result = await HttpClient.post("User/Register", { user: user });
          if (result) {
            setError("success");
            setIsLoaded(true);
          } else {
            setError("fail");
            setIsLoaded(true);
          }
        } else setError("capcha");
      } else setError("password");
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const RegisterMessage: React.FC = () => {
    if (error === "success")
      return (
        <TextAlert severity="success" error="You has registered successfully" />
      );
    if (error === "capcha")
      return <TextAlert severity="info" error="Capcha is incorrect" />;
    if (error === "fail")
      return <TextAlert severity="info" error="You has failed to register" />;
    if (error === "password")
      return (
        <TextAlert severity="info" error="Confirm password is incorrect" />
      );
    return <> </>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  useEffect(() => {
    Capcha.createCaptcha("register_capcha");
  }, []);
  useEffect(() => {
    if (error) Capcha.createCaptcha("register_capcha");
  }, [error]);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <IonPage>
      <IonContent>
        <div className="register">
          <div className="container_form">
            <div className="title">Get Started !</div>
            <div className="content">
              <form
                onSubmit={(e) => {
                  authenticate(e);
                }}
              >
                <div className="pt-2 pb-2">
                  <RegisterMessage />
                </div>
                <div className="user-details">
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="First Name must contain a-z A-Z."
                      label="First Name"
                      regex={/^[A-Za-z\d]{3,}$/}
                      isRequired={true}
                      handleChange={(event: any) => {
                        handleChange(event, "first_name");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Last Name must contain a-z A-Z."
                      label="Last Name"
                      regex={/^[A-Za-z\d]{3,}$/}
                      isRequired={true}
                      handleChange={(event: any) => {
                        handleChange(event, "last_name");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Email is invalid. Ex : viet@gmail.com."
                      label="Email : "
                      regex={
                        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
                      }
                      isRequired={true}
                      handleChange={(event: any) => {
                        handleChange(event, "email");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <PasswordInput
                      defaultValue=""
                      errorMessage="Password contains a-z, A-Z, 0-9 and minimum 8 characters"
                      label="Password : "
                      regex={
                        /^(?=[A-Za-z])(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[A-Za-z\d]{8,}$/
                      }
                      isRequired={true}
                      handleChange={(event: any) => {
                        handleChange(event, "password");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <PasswordInput
                      defaultValue=""
                      errorMessage="Password contains a-z, A-Z, 0-9 and minimum 8 characters"
                      label="Confirm Password : "
                      regex={/^/}
                      isRequired={true}
                      handleChange={(event: any) => {
                        handleChange(event, "confirm_password");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <NumberInput
                      defaultValue=""
                      label="Street Code : "
                      isRequired={false}
                      handleChange={(event: any) => {
                        handleChange(event, "street_code");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Invalid."
                      label="Street Name : "
                      regex={/^/}
                      isRequired={false}
                      handleChange={(event: any) => {
                        handleChange(event, "street_name");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Invalid."
                      label="Area : "
                      regex={/^/}
                      isRequired={false}
                      handleChange={(event: any) => {
                        handleChange(event, "area");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Invalid."
                      label="City : "
                      regex={/^/}
                      isRequired={false}
                      handleChange={(event: any) => {
                        handleChange(event, "city");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <BaseInput
                      defaultValue=""
                      errorMessage="Invalid."
                      label="Country : "
                      regex={/^/}
                      isRequired={false}
                      handleChange={(event: any) => {
                        handleChange(event, "country");
                      }}
                    />
                  </div>
                  <div className="input-box">
                    <div className="form-group d-flex">
                      <input
                        onChange={(e: any) => {
                          setCapchaCode(e.target.value);
                          setError("");
                        }}
                        type="text"
                        className="form-control me-2"
                      />
                      <Capcha.CapchaImage idName="register_capcha" />
                    </div>
                  </div>
                </div>

                <SubmitButton text="Register" hasLoaded={isLoaded} />
                <div style={{ marginTop: "80px" }} className="form-group">
                  <span>
                    Had an account ?{" "}
                    <Link style={{ textDecoration: "none" }} to="/login">
                      Login
                    </Link>
                  </span>
                </div>
                <div className="form-group">
                  <Link style={{ textDecoration: "none" }} to="/home">
                    <ArrowBackIcon /> Back Home
                  </Link>
                </div>
              </form>
            </div>
          </div>
        </div>
      </IonContent>
    </IonPage>
  );
};

export default Register;
