import React, { useState } from "react";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  label: any;
  handleChange: any;
  defaultValue: any;
  isRequired: Boolean;
  from: number;
  to: number;
}
//--------------------------------------------------------------
//--------------------------------------------------------------
const BaseInput: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  // declare variables
  const [error, setError] = useState("");
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const validateInput = (event: any) => {
    var checkingNumber = parseInt(event.target.value);
    if (checkingNumber) {
      if (checkingNumber > props.from && checkingNumber <= props.to) {
        setError("");
        props.handleChange(event);
      } else
        setError("This number must be from " + props.from + " to " + props.to);
    } else {
      if (props.isRequired)
        setError("This field is required and must contain 0-9.");
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="mb-3">
      <label className={"form-label " + (error ? "base-error" : "")} style={{}}>
        {props.label}
      </label>
      <input
        defaultValue={props.defaultValue}
        onChange={(event) => {
          validateInput(event);
        }}
        style={{ border: error ? "1px solid red" : "" }}
        type="number"
        className="form-control"
      />
      <p style={{ fontSize: "14px" }} className="base-error">
        {error}
      </p>
    </div>
  );
};
//--------------------------------------------------------------
//--------------------------------------------------------------
export default BaseInput;
