import $ from "jquery";
const HOST_NAME = "https://timesheet51.herokuapp.com/";
//const HOST_NAME = "http://localhost:5000/";

const post = async (path: string, data: any): Promise<any> => {
  var result = await $.post(HOST_NAME + path, data);
  return result;
};

const get = async (path: string): Promise<any> => {
  var result = await $.get(HOST_NAME + path);
  return result;
};

const upload = async (path: string, data: any): Promise<any> => {
  var result = await $.ajax({
    url: HOST_NAME + path,
    data: data,
    cache: false,
    contentType: false,
    processData: false,
    method: "POST",
    type: "POST",
  });
  return result;
};

export default { post, get, upload };
