import React, { useMemo } from "react";
import Checkbox from "@mui/material/Checkbox";
import "./Filter.css";
import { Button } from "@mui/material";

interface Props {
  data: any;
  filter: any;
  filterData: any;
  search: any;
}
var field = "";
const Filter: React.FC<Props> = (props: Props) => {
  const search = (e: any) => {
    props.search(e.target.value, field);
  };

  const handleChange = (value: any, name: any, index: number) => {
    props.filter.forEach((v: any) => {
      if (v.name === name) v.dataFilter[index] = value;
    });
  };

  const handleChangeCheck = (e: any, value: any, name: any) => {
    props.filter.forEach((v: any) => {
      if (v.name === name) {
        if (e.target.checked) v.dataFilter.push(value);
        else v.dataFilter = v.dataFilter.filter((vv: any) => vv !== value);
      }
    });
    console.log(props.filter);
  };

  const stringToDate = (str: any) => {
    console.log(str);

    var strDate = "";
    var d = "";
    for (var item = 0; item < str.length ; item++) {
      console.log(strDate);
      if (str[item] === "/") {
        if (item+1 < str.length) d = "-" + strDate + d;
        strDate = "";
      } else strDate = strDate + str[item];
    }
    d = strDate + d;
    console.log(d);

    return d;
  };

  const filterData = () => {
    var data = props.data;
    props.filter.map((value: any) => {
      if (value.dataFilter.length) {
        if (value.type === "date") {
          data = data.filter(
            (v: any) =>
              new Date(value.dataFilter[0]).getTime() <=
                new Date(stringToDate(v[value.name])).getTime() &&
              new Date(stringToDate(v[value.name])).getTime() <= new Date(value.dataFilter[1]).getTime()
          );
        }
        if (value.type === "number") {
          console.log("ok");
          
          data = data.filter(
            (v: any) =>
              parseInt(value.dataFilter[0]) <= v[value.name] &&
              v[value.name] <= parseInt(value.dataFilter[1])
          );
        }
        if (value.type === "checkbox") {
          data = data.filter(
            (v: any) => value.dataFilter.indexOf(v[value.name]) >= 0
          );
        }
      }
    });
    props.filterData(data);
  };

  const FilterCheckBox: React.FC = (props: any) => {
    const label = { inputProps: { "aria-label": "Checkbox demo" } };
    return (
      <div className="mb-3">
        <label className="form-label">{props.label}</label>
        <div>
          {props.data.map((value: any) => {
            return (
              <>
                <Checkbox
                  onChange={(e: any) => {
                    handleChangeCheck(e, value, props.name);
                  }}
                  {...label}
                />{" "}
                <span> {value} </span>
              </>
            );
          })}
        </div>
      </div>
    );
  };

  const FilterNumber: React.FC = (props: any) => {
    return (
      <div className="mb-3">
        <label className="form-label">{props.label}</label>
        <div className="row">
          <div className="d-flex filter_group align-items-center">
            <label className="form-label filter_label d-flex">Min</label>
            <input
              onChange={(e) => {
                handleChange(e.target.value, props.name, 0);
              }}
              className="form-control"
              type="number"
            />
          </div>
          <div className="d-flex filter_group align-items-center">
            <label className="form-label filter_label">Max</label>
            <input
              onChange={(e) => {
                handleChange(e.target.value, props.name, 1);
              }}
              className="form-control"
              type="number"
            />
          </div>
        </div>
      </div>
    );
  };

  const FilterDate: React.FC = (props: any) => {
    return (
      <div className="mb-3">
        <label className="form-label">{props.label}</label>
        <div className="row">
          <div className="d-flex filter_group align-items-center">
            <label className="form-label filter_label d-flex">Min</label>
            <input
              onChange={(e) => {
                handleChange(e.target.value, props.name, 0);
              }}
              className="form-control"
              type="date"
            />
          </div>
          <div className="d-flex filter_group align-items-center">
            <label className="form-label filter_label">Max</label>
            <input
              onChange={(e) => {
                handleChange(e.target.value, props.name, 1);
              }}
              className="form-control"
              type="date"
            />
          </div>
        </div>
      </div>
    );
  };
  return useMemo(() => {
    // The rest of your rendering logic
    return (
      <div className="filter w-100 pt-3 pb-3">
        <div className="filter-box">
          <div className="search pb-2">
            <input
              onChange={(e: any) => {
                search(e);
              }}
              placeholder="Search"
              type="text"
              className="form-control"
            />
            <select
              onChange={(e: any) => {
                field = e.target.value;
              }}
              className="form-select"
              aria-label="Default select example"
            >
              <option selected>Open a field</option>
              {props.filter.map((value: any) => {
                return <option value={value.name}>{value.label}</option>;
              })}
            </select>
          </div>
          {props.filter.map((value: any) => {
            if (value.type === "checkbox") return <FilterCheckBox {...value} />;
            if (value.type === "date") return <FilterDate {...value} />;
            if (value.type === "number") return <FilterNumber {...value} />;
          })}
          <div className="d-flex justify-content-end">
            <Button
              onClick={() => {
                filterData();
              }}
              variant="contained"
            >
              Filter
            </Button>
          </div>
        </div>
      </div>
    );
  }, []);
};

export default Filter;
