import React, { useState } from "react";
import TextField from "@mui/material/TextField";
import MobileDatePicker from "@mui/lab/MobileDatePicker";
import LocalizationProvider from "@mui/lab/LocalizationProvider";
import AdapterDateFns from "@mui/lab/AdapterDateFns";

interface Props {
  label: any;
  handleChange:any;
  name:any;
  value:any;
}

const DateInput: React.FC<Props> = (props: Props) => {
  const [error, setError] = useState("");
  const [value, setValue] = useState(new Date(props.value));

  const validateInput = (date: any) => {
    if (date)
    {
      props.handleChange(date,props.name);
      setValue(new Date(date));
    }
    else setError(props.label + " is required.");
  };

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <div style={{ position: "relative" }} className="mb-3">
        <label className={"form-label " + (error ? "base-error" : "")}>
          {props.label}
        </label>
        <div style={{ position: "relative" }}>
          <div
            style={{
              width:"100%",
              position: "absolute",
              top: "50%",
              left: "0",
              zIndex: 1,
              transform:"translateY(-50%)"
            }}
          >
            <MobileDatePicker
              inputFormat="yyyy/MM/dd"
              value={value}
              onChange={validateInput}
              renderInput={(params) => <TextField {...params} />}
            />
          </div>
          <input
            style={{ border: error ? "1px solid red" : "" }}
            type="text"
            className="form-control"
          />
        </div>
        <p className="base-error">{error}</p>
      </div>
    </LocalizationProvider>
  );
};

export default DateInput;
