// module
import * as React from "react";
import { useSelector } from "react-redux";
// components
import ListSubheader from "@mui/material/ListSubheader";
import List from "@mui/material/List";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import { NavLink } from "react-router-dom";
// icon
import AssessmentIcon from "@mui/icons-material/Assessment";
import GroupAddIcon from "@mui/icons-material/GroupAdd";
import GroupIcon from "@mui/icons-material/Group";
import NoteIcon from "@mui/icons-material/Note";
import DescriptionIcon from "@mui/icons-material/Description";
import TimelineIcon from "@mui/icons-material/Timeline";
import PersonIcon from "@mui/icons-material/Person";
import AccountTreeIcon from "@mui/icons-material/AccountTree";
import GroupsIcon from "@mui/icons-material/Groups";
import WorkIcon from "@mui/icons-material/Work";
import ChatIcon from "@mui/icons-material/Chat";
import QrCodeIcon from "@mui/icons-material/QrCode";
//--------------------------------------------------------------
//--------------------------------------------------------------
// sidebar for employee
const EmployeeSidebar: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const project_id = useSelector(
    (allSelectors: any) => allSelectors.project_id
  );
  const member_id = useSelector(
    (allSelectors: any) => allSelectors.memberInfo.member_id
  );
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <List
      sx={{ width: "100%", maxWidth: 360, bgcolor: "background.paper" }}
      component="nav"
      aria-labelledby="nested-list-subheader"
      subheader={
        <ListSubheader component="div" id="nested-list-subheader">
          Sidebar
        </ListSubheader>
      }
    >
      <NavLink
        className="base-link"
        to={"/dashboard/timesheet-summary/" + project_id + "-" + member_id}
      >
        <ListItemButton>
          <ListItemIcon>
            <TimelineIcon />
          </ListItemIcon>
          <ListItemText primary="Timesheet Summary" />
        </ListItemButton>
      </NavLink>
      <NavLink
        className="base-link"
        to={"/dashboard/expense-summary/" + project_id + "-" + member_id}
      >
        <ListItemButton>
          <ListItemIcon>
            <AssessmentIcon />
          </ListItemIcon>
          <ListItemText primary="Expense Summary" />
        </ListItemButton>
      </NavLink>
      <NavLink className="base-link" to="/dashboard/timesheet-report">
        <ListItemButton>
          <ListItemIcon>
            <NoteIcon />
          </ListItemIcon>
          <ListItemText primary="Timesheet Report" />
        </ListItemButton>
      </NavLink>
      <NavLink className="base-link" to="/dashboard/expense-report">
        <ListItemButton>
          <ListItemIcon>
            <DescriptionIcon />
          </ListItemIcon>
          <ListItemText primary="Expense Report" />
        </ListItemButton>
      </NavLink>
      <NavLink className="base-link" to="/dashboard/my-team">
        <ListItemButton>
          <ListItemIcon>
            <GroupIcon />
          </ListItemIcon>
          <ListItemText primary="My Team" />
        </ListItemButton>
      </NavLink>
      <NavLink className="base-link" to="/dashboard/chat">
        <ListItemButton>
          <ListItemIcon>
            <ChatIcon />
          </ListItemIcon>
          <ListItemText primary="Chat" />
        </ListItemButton>
      </NavLink>
      <NavLink className="base-link" to="/dashboard/timesheet-code">
        <ListItemButton>
          <ListItemIcon>
            <QrCodeIcon />
          </ListItemIcon>
          <ListItemText primary="Timesheet Code" />
        </ListItemButton>
      </NavLink>
    </List>
  );
};
