import React, { useState } from "react";

interface Props {
  label: any;
  regex: any;
  errorMessage: any;
  type: any;
  name:any;
  handleChange:any;
  value:any;
}

const TextInput: React.FC<Props> = (props: Props) => {
  const [error, setError] = useState("");
  const validateInput = (checkingText: string) => {
    if (checkingText) {
      const regexp = props.regex;
      const checkingResult = regexp.exec(checkingText);
      if (checkingResult !== null)
      {
        setError("");
        props.handleChange(checkingText,props.name)
      }
      else setError(props.errorMessage);
    } else setError(props.label + " is required.");
  };

  return (
    <div className="mb-3">
      <label className={"form-label " +  ( error ? "base-error" : "")} style={{}}>
        {props.label}
      </label>
      <input
        defaultValue={props.value}
        onChange={(event) => {
          validateInput(event.target.value);
        }}
        style={{ border: error ? "1px solid red" : "" }}
        type={props.type}
        className="form-control"
      />
      <p style={{fontSize:"14px"}} className="base-error">{error}</p>
    </div>
  );
};

export default TextInput;
