import { useState } from "react";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  label: string;
  name: string;
  handleChange: any;
  arr: any;
  idName: string;
  isSubmitted: boolean;
  defaultValue: any;
  isRequired: boolean;
}
//--------------------------------------------------------------
//--------------------------------------------------------------
const SelectInput: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const validateInput = (event: any) => {
    props.handleChange(event);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="mb-3">
      <label
        className={
          "form-label " +
          (!props.defaultValue && props.isSubmitted ? "base-error" : "")
        }
      >
        {props.label}
      </label>
      <select
        onChange={(event) => {
          validateInput(event);
        }}
        defaultValue={props.defaultValue}
        style={{
          border:
            !props.defaultValue && props.isSubmitted ? "1px solid red" : "",
        }}
        className="form-select"
      >
        <option selected>Open this select menu</option>
        {props.arr.map((value: any) => {
          return (
            <option value={value[props.idName]}>{value[props.name]}</option>
          );
        })}
      </select>
      {props.isRequired ? (
        <p className="base-error">
          {!props.defaultValue && props.isSubmitted
            ? "This field is required"
            : ""}
        </p>
      ) : (
        ""
      )}
    </div>
  );
};
//--------------------------------------------------------------
//--------------------------------------------------------------
export default SelectInput;
