import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import BaseTable from "../../../../components/Table/BaseTable";
import TableProgressor from "../../../../components/Table/TableProgressor";
import HttpClient from "../../../../services/HttpClient/HttpClient";
import AddIcon from "@mui/icons-material/Add";
import FilterListIcon from "@mui/icons-material/FilterList";
import DownloadIcon from "@mui/icons-material/Download";
import TextAlert from "../../../../components/Alert/Text/TextAlert";
import SubmitButton from "../../../../components/Button/SubmitButton";
import Filter from "../../../../components/Filter/Filter";
import DeleteIcon from "@mui/icons-material/Delete";
import Text from "../../../../components/Text/Text";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import CloseIcon from "@mui/icons-material/Close";
import SelectInput from "../../../../components/FormValidation/SelectInput";
import Alert_YESNO from "../../../../components/Alert/ButtonAlert/Alert_YESNO/Alert_YESNO";
import Tooltip from "@mui/material/Tooltip";
import IconButton from "@mui/material/IconButton";
import AreaInput from "../../../../components/FormValidation/AreaInput";
import TextInput from "../../../../components/FormValidation/TextInput";

import DateInput from "../../../../components/FormValidation/DateInput";
import ImgDetail from "../../../../components/ImgDetail/ImgDetail";
//--------------------------------------------------------------
//--------------------------------------------------------------
//global
var formValue: any = {
  project_id: "",
  purpose_or_reason: "",
  expense_type: "",
  expense_date: "",
  expense_cost: "",
};
//--------------------------------------------------------------
//--------------------------------------------------------------
const ExpenseReport: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const [message, setMessage] = useState("");
  const [files, setFiles] = useState([]);
  const [projects, setProjects] = useState([]);
  const [selectedMessage, setSelectedMessage] = useState("");
  const [submitType, setSubmitType] = useState("");
  const [isLoadedForm, setIsLoadedForm] = useState(true);
  const [isSubmited, setIsSubmited] = useState(false);
  const [isLoaded, setIsLoaded] = useState(true);
  const [isShownFilter, setIsShownFilter] = useState(false);
  const [isShownForm, setIsShownForm] = useState(false);
  const [rows, setRows] = useState([]);
  const [defaultRows, setDefaultRows] = useState([]);
  const selectors: any = useSelector((allSelectors) => allSelectors);
  var checkedRows: any = [];
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const load = async (): Promise<void> => {
    if (selectors.memberInfo.permission) {
      setIsLoaded(false);
      var newRows = await HttpClient.post("ExpenseReport/LoadExpense", {
        member_id: selectors.memberInfo.member_id,
      });
      newRows = stringToArray(newRows);
      if (newRows) {
        setRows(newRows);
        setDefaultRows(newRows);
        setIsLoaded(true);
      }
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const stringToArray = (oldValue: any) => {
    var newValue = [...oldValue];
    newValue.map((value) => {
      var a: any = [];
      var b = "";
      for (var i = 0; i < value.epath.length; i++) {
        b += value.epath[i] == "," ? "" : value.epath[i];
        if (value.epath[i + 1] == "," || i == value.epath.length - 1) {
          console.log(b);
          a.push(b);
          b = "";
        }
      }
      value.epath = a;
    });
    return newValue;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  var headCells: readonly any[] = [
    {
      id: "",
      numeric: false,
      disablePadding: true,
      label: "Number",
    },
    {
      id: "project_name",
      numeric: false,
      disablePadding: true,
      label: "Project Name",
    },
    {
      id: "team_name",
      numeric: true,
      disablePadding: false,
      label: "Team Name",
    },
    {
      id: "expense_status",
      numeric: true,
      disablePadding: false,
      label: "Status",
    },
    {
      id: "expense_cost",
      numeric: true,
      disablePadding: false,
      label: "Cost",
    },
    {
      id: "expense_type",
      numeric: true,
      disablePadding: false,
      label: "Type",
    },
    {
      id: "",
      numeric: true,
      disablePadding: false,
      label: "",
    },
  ];
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getRows = async (cRows: any): Promise<void> => {
    checkedRows = cRows;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const submit = async (e: any): Promise<void> => {
    e.preventDefault();
    var isValid = true;
    for (var item in formValue) if (!formValue[item]) isValid = false;
    alert(files.length);
    if (isValid) {
      setIsSubmited(true);
      if (submitType === "add" && files.length) {
        setIsLoadedForm(false);
        formValue.employee_id = selectors.memberInfo.member_id;
        formValue.expense_date = dateToString(formValue.expense_date);
        var result = await HttpClient.post("ExpenseReport/AddExpense", {
          expense: formValue,
        });
        var imgFiles = [...files],
          filesPath = [];
        var resultUpload, resultAddPath;
        for (var i = 0; i < imgFiles.length; i++) {
          var file = imgFiles[i];
          var data = new FormData();
          data.append("expense_image", file);
          resultUpload = await HttpClient.upload(
            "ExpenseReport/UploadFile",
            data
          );
          filesPath.push(resultUpload);
          if (resultUpload)
            resultAddPath = await HttpClient.post(
              "ExpenseReport/AddExpenseImage",
              {
                expense: { expense_id: result, expense_path: resultUpload },
              }
            );
        }
        if (result && resultUpload && resultAddPath) {
          var newRow: any = { ...formValue };
          newRow.epath = filesPath;
          var newRows: any = [...defaultRows];
          projects.map((value: any) => {
            if (value.project_id.toString() === newRow.project_id.toString()) {
              newRow = { ...newRow, ...value };
              console.log(value);
              return;
            }
          });
          newRows.push(newRow);
          setDefaultRows(newRows);
          setRows(newRows);
          setIsLoadedForm(true);
          setMessage("success");
        } else {
          setIsLoadedForm(true);
          setMessage("warning");
        }
      }
      if (submitType === "edit") {
        setIsLoadedForm(false);
        var fv = { ...formValue };
        if (!files.length) {
          fv.epath = [];
        }
        fv.employee_id = selectors.memberInfo.member_id;
        fv.expense_date = dateToString(formValue.expense_date);
        var result = await HttpClient.post("ExpenseReport/EditExpense", {
          expense: fv,
        });
        var imgFiles = [...files],
          filesPath = [];
        var resultUpload, resultAddPath;
        for (var i = 0; i < imgFiles.length; i++) {
          var file = imgFiles[i];
          var data = new FormData();
          data.append("expense_image", file);
          resultUpload = await HttpClient.upload(
            "ExpenseReport/UploadFile",
            data
          );
          filesPath.push(resultUpload);
          if (resultUpload)
            resultAddPath = await HttpClient.post(
              "ExpenseReport/AddExpenseImage",
              {
                expense: {
                  expense_id: formValue.expense_id,
                  expense_path: resultUpload,
                },
              }
            );
        }
        if (result) {
          var newRow: any = { ...formValue };
          if (filesPath.length) newRow.epath = filesPath;
          var newRows: any = [...defaultRows];
          for (var i = 0; i < newRows.length; i++) {
            if (
              newRows[i].expense_id.toString() === newRow.expense_id.toString()
            ) {
              newRows[i] = { ...newRows[i], ...newRow };
              break;
            }
          }
          setDefaultRows(newRows);
          setRows(newRows);
          setIsLoadedForm(true);
          setMessage("success");
        } else {
          setMessage("warning");
          setIsLoadedForm(true);
        }
      }
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  var filter = [
    {
      label: "Project Name",
      name: "project_name",
      type: "empty",
      dataFilter: [],
    },
    {
      label: "Team Name",
      name: "team_name",
      type: "empty",
      dataFilter: [],
    },
    {
      label: "Admin Name",
      name: "admin_name",
      type: "empty",
      dataFilter: [],
    },
    {
      label: "Cost",
      name: "expense_cost",
      type: "number",
      dataFilter: [],
    },
    {
      label: "Expense Type",
      type: "checkbox",
      name: "expense_type",
      data: ["Travel", "Meal", "Hotel", "Transport", "Mobile", "Other"],
      dataFilter: [],
    },
  ];
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const filterField = () => {
    filter.map((value: any) => {
      if (value.type === "checkbox")
        rows.map((member: any) => {
          if (value.data.indexOf(member[value.name]) < 0)
            value.data.push(member[value.name]);
        });
    });
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const filterData = (data: any) => {
    setRows(data);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const dateToString = (oldValue: any): string => {
    var dateStr =
      new Date(oldValue).getFullYear() +
      "-" +
      (new Date(oldValue).getMonth() + 1) +
      "-" +
      new Date(oldValue).getDate();
    return dateStr;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const edit = (data: any) => {
    formValue = { ...data };
    setIsShownForm(!isShownForm);
    setSubmitType("edit");
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const search = (value: any, name: any) => {
    if (value) {
      var temp = defaultRows;
      temp = temp.filter(
        (v: any) => v[name].toUpperCase().indexOf(value.toUpperCase()) >= 0
      );
      setDefaultRows(temp);
    } else setDefaultRows(rows);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const ProcessedSelectionMessage: React.FC = () => {
    if (selectedMessage === "success")
      return (
        <TextAlert
          error="Project has deleted successfully"
          severity="success"
        />
      );
    else if (selectedMessage === "warning")
      return (
        <TextAlert error="Project has failed to delete" severity="warning" />
      );
    else return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const ProcessedFormMessage: React.FC = () => {
    if (message === "success")
      return (
        <TextAlert error="This has done successfully" severity="success" />
      );
    if (message === "success")
      return <TextAlert error="This has failed to do" severity="warning" />;
    else return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const hccFormValue = (value: string, name: string) => {
    var newValue: any = { ...formValue };
    newValue[name] = value;
    formValue = newValue;
    setMessage("");
    setIsSubmited(false);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const hcFormValue = (e: any) => {
    var newValue: any = { ...formValue };
    newValue[e.target.name] = e.target.value;
    formValue = newValue;
    setMessage("");
    setIsSubmited(false);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const Form: React.FC = () => {

    const ImageForm: React.FC = () => {
      if (files.length) {
        return files.map((value: any) => {
          return (
            <div className="form-bg_input-group">
              <ImgDetail
                type="client"
                biStyle={{}}
                siStyle={{}}
                src={URL.createObjectURL(value)}
              />
            </div>
          );
        });
      } else {
        if (formValue?.epath) {
          return formValue.epath.map((value: any) => {
            return (
              <div className="form-bg_input-group">
                <ImgDetail
                  type="server"
                  biStyle={{}}
                  siStyle={{}}
                  src={value}
                />
              </div>
            );
          });
        }
        return <></>;
      }
    };

    useEffect(() => {
    }, []);
    console.log(files.length);

    console.log(files.length && isSubmited ? "true" : "false");

    if (isShownForm) {
      return (
        <>
          <div className="base-dark-bg" />
          <div className="form-bg">
            <form
              onSubmit={submit}
              className="light-border m-auto form-bg_two-input"
            >
              <CloseIcon
                onClick={() => {
                  setIsShownForm(!isShownForm);
                }}
                className="form-bg_icon"
              />
              <div className="mt-5 px-3">
                <ProcessedFormMessage />
              </div>
              <h4 className="m-0 px-4">Expense Detail</h4>
              <div className="p-4">
                <div className="form-bg_input-group">
                  <SelectInput
                    value={formValue.project_id}
                    handleChange={hccFormValue}
                    label="Project Name"
                    name="project_id"
                    arr={projects}
                    text="project_name"
                    error={formValue}
                    hasSubmited={isSubmited}
                  />
                </div>
                <div className="form-bg_input-group">
                  <SelectInput
                    value={formValue.expense_type}
                    handleChange={hccFormValue}
                    label="Expense Type"
                    name="expense_type"
                    arr={[
                      { expense_type: "Travel" },
                      { expense_type: "Meal" },
                      { expense_type: "Hotel" },
                      { expense_type: "Transport" },
                      { expense_type: "Mobile" },
                      { expense_type: "Other" },
                    ]}
                    text="expense_type"
                    error={formValue}
                    hasSubmited={isSubmited}
                  />
                </div>
                <div className="form-bg_input-group">
                  <DateInput
                    handleChange={hccFormValue}
                    name="expense_date"
                    label="Expense Date : "
                    value={formValue.expense_date}
                  />
                </div>
                <div className="form-bg_input-group">
                  <TextInput
                    value={formValue.expense_cost}
                    errorMessage="This field contains digits."
                    label="Cost"
                    regex={/^/}
                    type="number"
                    handleChange={hccFormValue}
                    name="expense_cost"
                  />
                </div>
                <AreaInput
                  value={formValue.purpose_or_reason}
                  errorMessage="This field must contains a-z A-Z 0-9 and minimum 3 characters."
                  label="Purpose or Reason"
                  regex={/^/}
                  name="purpose_or_reason"
                  handleChange={hccFormValue}
                />
                <div className="mb-3">
                  <label
                    htmlFor="formFile"
                    className={
                      "form-label " +
                      (files.length === 0 && isSubmited && submitType === "add"
                        ? "base-error"
                        : "")
                    }
                  >
                    Expense Image
                  </label>
                  <input
                    onChange={(e: any) => {
                      var selectedFiles = e.target.files;
                      var filesArr: any = [];
                      for (var i = 0; i < selectedFiles.length; i++)
                        filesArr.push(selectedFiles[i]);
                      setMessage("");
                      setFiles(filesArr);
                    }}
                    name="expense_image"
                    multiple
                    id="expense_image"
                    className="form-control"
                    type="file"
                  />
                  <p style={{ fontSize: "14px" }} className="base-error">
                    {files.length === 0 && isSubmited && submitType === "add"
                      ? "This field is required"
                      : ""}
                  </p>
                </div>
                <ImageForm />
                <SubmitButton
                  hasLoaded={isLoadedForm}
                  text={submitType === "add" ? "Add Expense" : "Edit Expense"}
                />
              </div>
            </form>
          </div>
        </>
      );
    }
    return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const FilterForm: React.FC = () => {
    if (isShownFilter) {
      return (
        <Filter
          search={search}
          data={defaultRows}
          filter={filter}
          filterData={filterData}
        />
      );
    }
    return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const DataTable: React.FC = () => {
    if (isLoaded) {
      return (
        <BaseTable
          checkBoxValue=""
          fieldCheckBox=""
          checkedRows={getRows}
          icon={() => {
            return (
              <Tooltip title="Delete">
                <IconButton style={{ position: "relative" }}>
                  <DeleteIcon />
                  <Alert_YESNO
                    yesFunction={async () => {
                      var checkedData = defaultRows.filter(
                        (value: any, index: any) =>
                          checkedRows.indexOf(index.toString()) >= 0
                      );
                      var result = await HttpClient.post(
                        "ExpenseReport/DeleteExpense",
                        { rows: checkedData }
                      );
                      if (result) {
                        var newData = defaultRows.filter(
                          (value: any, index: any) =>
                            checkedRows.indexOf(index.toString()) < 0
                        );
                        setDefaultRows(newData);
                        setRows(newData);
                        setSelectedMessage(result ? "success" : "warning");
                      } else {
                        setSelectedMessage(result ? "success" : "warning");
                      }
                    }}
                    noFunction={() => {}}
                    header="Notification"
                    message="Do you want to remove this."
                    yesText="Ok"
                    noText="Cancel"
                  />
                </IconButton>
              </Tooltip>
            );
          }}
          tool={(row: any) => {
            return (
              <Text>
                <span
                  onClick={() => {
                    edit(row);
                  }}
                  style={{ cursor: "pointer" }}
                  className="d-flex justify-content-center"
                >
                  <ModeEditIcon /> Edit
                </span>
              </Text>
            );
          }}
          chooseRow={() => {}}
          rows={defaultRows}
          headCells={headCells}
        />
      );
    }
    return <TableProgressor />;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  //filterField();
  //component did mount
  useEffect(() => {
    load();
  }, []);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="p-5">
      <p className="title-page light-border-bottom mb-3">Expense Report</p>
      <div className="row w-100 m-0 mb-3">
        <div className="col-xl-6 col-12 mb-2"></div>
        <div className="col-xl-6 col-12 mb-2">
          <div className="table-tool">
            <Text
              onClick={() => {
                setIsShownForm(!isShownForm);
                setSubmitType("add");
              }}
              style={{ cursor: "pointer" }}
            >
              <p className="d-flex align-items-center">
                <AddIcon />
                <span style={{ marginLeft: "10px" }}>Add Expense</span>
              </p>
            </Text>
            <Text
              style={{ marginLeft: "10px", cursor: "pointer" }}
              onClick={() => {
                setIsShownFilter(!isShownFilter);
              }}
            >
              <p className="d-flex align-items-center">
                <FilterListIcon />
                <span style={{ marginLeft: "10px" }}>Filter</span>
              </p>
            </Text>
            <Text style={{ marginLeft: "10px", cursor: "pointer" }}>
              <p className="d-flex align-items-center">
                <DownloadIcon />
                <span style={{ marginLeft: "10px" }}>Export</span>
              </p>
            </Text>
          </div>
        </div>
      </div>
      <Form />
      <FilterForm />
      <ProcessedSelectionMessage />
      <DataTable />
    </div>
  );
};

export default ExpenseReport;
