// components
import MyAvatar from "../../../../../components/Avatar/Avatar";
import LeftMessage from "./Message/LeftMessage/LeftMessage";
import RightMessage from "./Message/RightMessage/RightMessage";
// module
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
// icon
import SendIcon from "@mui/icons-material/Send";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
// stylesheet
import "./ChatBox.css";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  member_name: string;
  member_id: string;
  avatar: string;
  messages: any;
  hideChatBox: any;
  sendMessage:any;
}
//--------------------------------------------------------------
//--------------------------------------------------------------
var content = "";
const ChatBox: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  // declare variables
  const memberInfo = useSelector(
    (allSelectors: any) => allSelectors.memberInfo
  );
  const socket = useSelector((allSelectors: any) => allSelectors.socket);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const sendMessage = (content: any): void => {
    var sent_date =
      new Date().getFullYear() +
      "-" +
      (new Date().getMonth() + 1) +
      "-" +
      new Date().getDate();
    var message: any = {
      sender: memberInfo.member_id,
      receiver: props.member_id,
      content: content,
      sent_date: sent_date,
      status: false,
    };
    props.sendMessage(message);
    socket.emit("SendMessage", message);
    var a: any = document.getElementById("chat-box_footer_input");
    a.value = "";
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="chat-box">
      <div className="chat-box_header m-auto d-flex align-items-center px-4">
        <ArrowBackIosIcon
          style={{ cursor: "pointer" }}
          onClick={props.hideChatBox}
        />
        <MyAvatar src={props.avatar} name={props.member_name} />
        <p className="m-0 ms-3">{props.member_name}</p>
      </div>
      <div className="chat-box_body">
        {props.messages.map((value: any) => {
          if (memberInfo.member_id !== value.sender)
            return (
              <LeftMessage
                member_name={props.member_name}
                sent_date={new Date(value.sent_date)}
                content={value.content}
                avatar={props.avatar}
              />
            );
          else
            return (
              <RightMessage
                member_name={memberInfo.first_name + " " + memberInfo.last_name}
                sent_date={new Date(value.sent_date)}
                content={value.content}
                avatar={props.avatar}
              />
            );
        })}
      </div>
      <div className="chat-box_footer">
        <input
          onKeyDown={(e: any) => {
            if (e.keyCode === 13) sendMessage(e.target.value);
          }}
          onChange={(e: any) => {
            content = e.target.value;
          }}
          id="chat-box_footer_input"
          placeholder="Type a message"
          className="form-control me-3"
          type="text"
        />
        <div
          onClick={() => {
            sendMessage(content);
          }}
          className="chat-box_footer_icon"
        >
          <SendIcon fontSize="medium" />
        </div>
      </div>
    </div>
  );
};
//--------------------------------------------------------------
//--------------------------------------------------------------
export default ChatBox;
