import { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import BaseTable from "../../../../components/Table/BaseTable";
import TableProgressor from "../../../../components/Table/TableProgressor";
import HttpClient from "../../../../services/HttpClient/HttpClient";
import CloseIcon from "@mui/icons-material/Close";
import FilterListIcon from "@mui/icons-material/FilterList";
import DownloadIcon from "@mui/icons-material/Download";
import Filter from "../../../../components/Filter/Filter";
import FindInPageIcon from "@mui/icons-material/FindInPage";
import Text from "../../../../components/Text/Text";
import AddBoxIcon from "@mui/icons-material/AddBox";
import Alert_YESNO from "../../../../components/Alert/ButtonAlert/Alert_YESNO/Alert_YESNO";
import Tooltip from "@mui/material/Tooltip";
import IconButton from "@mui/material/IconButton";
import TextAlert from "../../../../components/Alert/Text/TextAlert";
import "./AllExpense.css";
//--------------------------------------------------------------
//--------------------------------------------------------------
var headCells: readonly any[] = [
  {
    id: "",
    numeric: false,
    disablePadding: true,
    label: "Number",
  },
  {
    id: "member_name",
    numeric: true,
    disablePadding: false,
    label: "Member Name",
  },
  {
    id: "expense_cost",
    numeric: true,
    disablePadding: false,
    label: "Cost",
  },
  {
    id: "expense_status",
    numeric: true,
    disablePadding: false,
    label: "Status",
  },
  {
    id: "expense_type",
    numeric: true,
    disablePadding: false,
    label: "Type",
  },
  {
    id: "",
    numeric: true,
    disablePadding: false,
    label: "",
  },
];
//--------------------------------------------------------------
//--------------------------------------------------------------
var choosenRow: any = {};
//--------------------------------------------------------------
//--------------------------------------------------------------
const AllTimesheet: React.FC = () => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const [rows, setRows] = useState<any>([]);
  const [defaultRows, setDefaultRows] = useState<any>([]);
  const [isLoaded, setIsLoaded] = useState<boolean>(false);
  const [message, setMessage] = useState<string>("");
  const [isShownForm, setIsShownForm] = useState<boolean>(false);
  const [isShownFilter, setIsShownFilter] = useState<boolean>(false);
  const [selectionMessage, setSelectionMessage] = useState<string>("");
  const selectors: any = useSelector((allSelectors) => allSelectors);
  var checkedRows: any = [];
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  // load timesheet list
  const load = async (): Promise<void> => {
    if (selectors.memberInfo.permission) {
      setIsLoaded(false);
      var result = await HttpClient.post("AllExpense/LoadExpenseByManager", {
        manager_id: selectors.memberInfo.member_id,
      });
      var newRows = Object.assign([], result);
      newRows.map((value: any) => {
        value.work_date = new Date(value.work_date).toLocaleDateString();
      });
      if (newRows) {
        setRows(newRows);
        setDefaultRows(newRows);
      }
      setIsLoaded(true);
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const getcheckedRows = (rows: any) => {
    checkedRows = rows;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const submit = async (e: any): Promise<void> => {
    // e.preventDefault();
    // var isValid = true;
    // for (var item in timesheet) if (!timesheet[item]) isValid = false;
    // if (isValid) {
    //   setState({ ...state, isLoaded: false });
    //   timesheet.organization_id = selectors.memberInfo.organization_id;
    //   timesheet.from_date =
    //     new Date(timesheet.from_date).getFullYear() +
    //     "-" +
    //     new Date(timesheet.from_date).getMonth() +
    //     "-" +
    //     new Date(timesheet.from_date).getDay();
    //   timesheet.to_date =
    //     new Date(timesheet.to_date).getFullYear() +
    //     "-" +
    //     new Date(timesheet.to_date).getMonth() +
    //     "-" +
    //     new Date(timesheet.to_date).getDay();
    //   var result = await HttpClient.post("create-timesheet", timesheet);
    //   if (result) setState({ ...state, isLoaded: true, message: "success" });
    //   else setState({ ...state, isLoaded: true, message: "warning" });
    // }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  var filter = [
    {
      label: "Member Name",
      name: "member_name",
      type: "empty",
      dataFilter: [],
    },
    {
      label: "Work Date",
      name: "work_date",
      type: "date",
      dataFilter: [],
    },
    {
      label: "Hours",
      name: "hours",
      type: "number",
      dataFilter: [],
    },
    {
      label: "Status",
      type: "checkbox",
      name: "status",
      data: ["Approved", "Unapproved"],
      dataFilter: [],
    },
  ];
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const filterField = () => {
    filter.map((value: any) => {
      if (value.type === "checkbox")
        rows.map((member: any) => {
          if (value.data.indexOf(member[value.name]) < 0)
            value.data.push(member[value.name]);
        });
    });
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const filterData = (data: any) => {
    console.log(data);
    setDefaultRows(data);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const detail = (data: any) => {
    console.log(data);
    choosenRow = { ...data };
    setIsShownForm(true);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const search = (value: any, name: any) => {
    console.log(name);
    if (value) {
      var temp = defaultRows;
      temp = temp.filter(
        (v: any) => v[name].toUpperCase().indexOf(value.toUpperCase()) >= 0
      );
      console.log(temp);
      setDefaultRows(temp);
    } else setDefaultRows(rows);
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const combineExpenseByImage = () => {
    var newData: any = [];
    defaultRows.map((value: any) => {
      var newValue = { ...value };
      newValue.expense_path = [];
      newValue.expense_path.push(value.expense_path);
      var hasExisted = newData.some(
        (v: any) => value.expense_id === v.expense_id
      );
      if (!hasExisted) newData.push(newValue);
      else {
        newData.map((v: any) => {
          if (v.expense_id === value.expense_id)
            v.expense_path.push(value.expense_path);
        });
      }
    });
    return newData;
  };
  var data = defaultRows;
  filterField();
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const ProcessedSelectionMessage: React.FC = () => {
    if (selectionMessage === "success")
      return (
        <TextAlert
          error="Expense has approved successfully"
          severity="success"
        />
      );
    else if (selectionMessage === "warning")
      return (
        <TextAlert error="Expense has failed to approved" severity="warning" />
      );
    else return <></>;
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------

  //component did mount
  useEffect(() => {
    load();
  }, []);
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="p-5 all-timesheet">
      <p className="title-page light-border-bottom mb-3">All Expense</p>
      <div className="row w-100 m-0 mb-3">
        <div className="col-xl-6 col-12 mb-2"></div>
        <div className="col-xl-6 col-12 mb-2">
          <div className="d-flex justify-content-end">
            <Text
              className="base-text"
              style={{ marginLeft: "10px", cursor: "pointer" }}
              onClick={() => {
                setIsShownFilter(!isShownFilter);
              }}
            >
              <span className="d-flex align-items-center">
                <FilterListIcon />
                <span style={{ marginLeft: "10px" }}>Filter</span>
              </span>
            </Text>
            <Text style={{ marginLeft: "10px", cursor: "pointer" }}>
              <span className="d-flex align-items-center">
                <DownloadIcon />
                <span style={{ marginLeft: "10px" }}>Export</span>
              </span>
            </Text>
          </div>
        </div>
      </div>
      {isShownForm ? (
        <>
          <div className="base-dark-bg" />
          <div className="form-bg">
            <form
              onSubmit={submit}
              className="light-border m-auto form-bg_two-input"
            >
              <CloseIcon
                onClick={() => {
                  setIsShownForm(false);
                }}
                className="form-bg_icon"
              />
              <h4 className="m-0 p-4">Timesheet Detail</h4>
              <div className="p-4">
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Project Name : </label>
                  <input
                    value={choosenRow.project_name}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Member Name : </label>
                  <input
                    value={choosenRow.member_name}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Position Name : </label>
                  <input
                    value={choosenRow.position_name}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Department Name : </label>
                  <input
                    value={choosenRow.department_name}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Cost : </label>
                  <input
                    value={choosenRow.expense_cost}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3">
                  <label className="form-label">Purpose or Reason : </label>
                  <textarea
                    disabled
                    className="form-control"
                    value={choosenRow.purpose_or_reason}
                  ></textarea>
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Expense Type : </label>
                  <input
                    value={choosenRow.expense_type}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
                <div className="mb-3 form-bg_input-group">
                  <label className="form-label">Status : </label>
                  <input
                    value={choosenRow.expense_status}
                    disabled
                    type="text"
                    className="form-control"
                  />
                </div>
              </div>
            </form>
          </div>
        </>
      ) : (
        ""
      )}
      {isShownFilter ? (
        <Filter
          search={search}
          data={data}
          filter={filter}
          filterData={filterData}
        />
      ) : (
        ""
      )}
      <ProcessedSelectionMessage />
      {isLoaded ? (
        <BaseTable
          checkBoxValue="Unapproved"
          fieldCheckBox="expense_status"
          checkedRows={getcheckedRows}
          icon={() => {
            return (
              <Tooltip title="Approve">
                <IconButton style={{ position: "relative" }}>
                  <AddBoxIcon />
                  <Alert_YESNO
                    yesFunction={async () => {
                      // var checkedData = defaultRows.filter(
                      //   (value: any, index: any) =>
                      //     checkedRows.indexOf(index.toString()) >= 0
                      // );
                      // var result = await HttpClient.post(
                      //   "AllExpense/ApproveExpense",
                      //   { rows: checkedData }
                      // );
                      // if (result) {
                      //   var newData: any = [...defaultRows];
                      //   checkedRows.map((value: any) => {
                      //     newData[value].expense_status = "Approved";
                      //   });
                      //   setState({
                      //     ...state,
                      //     rows: newData,
                      //     defaultRows: newData,
                      //     selectionMessage: result ? "success" : "warning",
                      //   });
                      // } else {
                      //   setState({
                      //     ...state,
                      //     selectionMessage: result ? "success" : "warning",
                      //   });
                      // }
                    }}
                    noFunction={() => {}}
                    header="Notification"
                    message="Do you want to approve this."
                    yesText="Ok"
                    noText="Cancel"
                  />
                </IconButton>
              </Tooltip>
            );
          }}
          tool={(row: any) => {
            return (
              <Text>
                <span
                  onClick={() => {
                    detail(row);
                  }}
                  style={{ cursor: "pointer" }}
                  className="d-flex justify-content-center"
                >
                  <FindInPageIcon /> Detail
                </span>
              </Text>
            );
          }}
          chooseRow={() => {}}
          rows={combineExpenseByImage()}
          headCells={headCells}
        />
      ) : (
        <TableProgressor />
      )}
    </div>
  );
};

export default AllTimesheet;
