import React, { useState } from "react";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  label: any;
  regex: any;
  errorMessage: any;
  handleChange: any;
  defaultValue: any;
  isRequired: Boolean;
}
//--------------------------------------------------------------
//--------------------------------------------------------------
const PasswordInput: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  // declare variables
  const [error, setError] = useState("");
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  const validateInput = (event: any) => {
    var checkingText = event.target.value;
    if (checkingText) {
      const regexp = props.regex;
      const checkingResult = regexp.exec(checkingText);
      if (checkingResult !== null) {
        setError("");
        props.handleChange(event);
      } else setError(props.errorMessage);
    } else {
      if (props.isRequired) setError("This field is required.");
    }
  };
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div className="mb-3">
      <label className={"form-label " + (error ? "base-error" : "")} style={{}}>
        {props.label}
      </label>
      <input
        defaultValue={props.defaultValue}
        onChange={(event) => {
          validateInput(event);
        }}
        style={{ border: error ? "1px solid red" : "" }}
        type="password"
        className="form-control"
      />
      <p style={{ fontSize: "14px" }} className="base-error">
        {error}
      </p>
    </div>
  );
};
//--------------------------------------------------------------
//--------------------------------------------------------------
export default PasswordInput;
