import React, { useState, useMemo } from "react";

interface Props {
  label: any;
  regex: any;
  errorMessage: any;
  handleChange: any;
  defaultValue: any;
}

const TextArea: React.FC<Props> = (props: Props) => {
  const [error, setError] = useState("");
  const validateInput = (event: any) => {
    var checkingText = event.target.value;
    if (checkingText) {
      const regexp = props.regex;
      const checkingResult = regexp.exec(checkingText);
      if (checkingResult !== null) {
        setError("");
        props.handleChange(event);
      } else setError(props.errorMessage);
    } else setError("This field is required.");
  };
  return (
    <div className="mb-3">
      <label
        className="form-label"
        style={{ color: "form-label " + (error ? "red" : "") }}
      >
        {props.label}
      </label>
      <textarea
        defaultValue={props.defaultValue}
        onChange={(event: any) => {
          validateInput(event);
        }}
        style={{ border: error ? "1px solid red" : "" }}
        className="form-control"
      />
      <p className="base-error">{error}</p>
    </div>
  );
};

export default TextArea;
