import * as React from "react";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import Friend from "./Friend/Friend";
//--------------------------------------------------------------
//--------------------------------------------------------------
interface Props {
  showChatBox: any;
  friends:any;
}
//--------------------------------------------------------------
//--------------------------------------------------------------
const ListFriend: React.FC<Props> = (props: Props) => {
  //--------------------------------------------------------------
  //--------------------------------------------------------------
  return (
    <div style={{ height: "100%",borderRight:"1px solid #e2e8f0" }}  className="list-friend">
      <div className="py-4 px-5 light-border-bottom">
        <input
          className="form-control mr-sm-2"
          type="search"
          placeholder="Search friend"
        />
      </div>
      <List
        sx={{ width: "100%", maxWidth: "inherit", bgcolor: "background.paper" }}
      >
        {props.friends.map((value: any) => {
          return (
            <div
              onClick={() => {
                props.showChatBox(value);
              }}
            >
              <Friend
                avatar={value.avatar}
                name={value.member_name}
                message={value.recent_message}
                isOnline={value.isOnline}
                recentDate={new Date(value.recent_date)}
              />
              <Divider variant="inset" component="li" />
            </div>
          );
        })}
      </List>
    </div>
  );
  //--------------------------------------------------------------
  //--------------------------------------------------------------
};

export default ListFriend;
